#page-description {
    margin-bottom: 10px; }

#post img {
    display: block;
    margin: 0 auto; }

#post-page {
    margin: 0 auto;
    display: grid;
    max-width: 1200px;
    grid-template-columns: 800px minmax(200px, 1fr); }

#post-column-left {
    padding: 0 20px 20px 20px;
    box-sizing: border-box; }

#post-column-right {
    box-sizing: border-box;
    border-left: 1px solid #E3E3E3; }

#social {
    padding: 20px; }

#social-links {
    margin-left: 0;
    font-size: 1.5em; }

#social-links a {
    text-decoration: none; }

#related-posts {
    padding: 10px;
    border-top: 1px solid #E3E3E3; }

#related-posts h3 {
    padding: 0 10px 5px; }

.related-post {
    float: left;
    margin-bottom: 10px; }

.related-post a {
    float: left;
    padding: 15px;
    margin-bottom: 5px;
    background-color: #F8F8FB;
    border: 1px solid #E5E5E5;
    border-radius: 7px;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px; }

.related-post a:hover {
    background-color: #F3F3F8;
    color: #111;
    border: 1px solid #888; }

.related-post a span {
    float: left; }

.related-post-title {
    font-weight: bold; }

.related-post-excerpt {
    margin-top: 7px;
    font-size: 0.85em; }

.title-container {
    display: flex;
    float: left;
    margin: 10px 0 20px 0;
    width: 100%; }

.title-container img {
    margin-top: 4px;
    height: 60px; }

.title-container h1 {
    padding-left: 20px; }

@media screen and (max-width: 1000px) {

    #post-page {
        grid-template-columns: 1fr; }

    #post-column-left {
        margin: 0 auto;
        max-width: 750px; }

    #post-column-right {
        margin: 0 auto;
        grid-row: 2;
        min-width: 100px;
        border-top: 2px solid #EBEBEB;
        border-left: none; } }
